.c-item-box {
    &.c-item-active {
        box-shadow: 0 0 15px #071769;
    }
    color: #FFFFFF;
    background: url(../images/bg.jpg) no-repeat center center;
    background-size: cover;
    margin: 18px 8px;
    padding: 10px 8px 8px 8px;
    border-radius: 5px;
    position: relative;
    &:first-child {
        margin-top: 2px;
    }
    &.acitve {
        box-shadow: 0 0 8px rgba(16,142,233,.5);
    }
    .c-item-closebtn {
        position: absolute;
        right: -6px;
        top: -13px;
        z-index: 99;
        color: #232323;
        width: 26px;
        height: 26px;
    }
    .c-item-head{
        display: flex;
        flex-direction: row;
        align-items: center;
        .c-item-playbtn {
            .c-item-btn();
            &::after {
                content: '';
                position: absolute; 
                top: 9px;
                left: 15px;
                width: 0;
                height: 0;
                border: 9px solid;
                border-color:  transparent  transparent transparent #ffffff;
            }
        }
        .c-item-pausebtn {
            .c-item-btn();
            &::after {
                content: '';
                position: absolute;
                top: 12px;
                left: 12px;
                width:12px;
                height: 12px;
                background-image: linear-gradient(to right, #ffffff 33%, transparent 33%, transparent 66%, #ffffff 66%, #ffffff 100%);
            }
        }
        .c-item-audioname {
            font-size: 16px;
            flex: 1;
            min-width: 0;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            padding: 0 10px;
            text-align: left;
        }
        .c-item-volume-text {
            font-size: 14px;
            margin:0 3px 0 4px;
        }
        .icon-yinliang1 {
            font-size: 22px;
            position: relative;
            color: #FFFFFF;
            &.disabled::after{
                content: '';
                width: 22px;
                height: 12px;
                color: #ff0000;
                position: absolute;
                top: 2px;
                left: 3px;
                z-index: 999;
                border-bottom: solid 2px red;
                transform: rotate(45deg);
            }
        }
        .c-item-volume {
            width: 80px;
            padding-right: 10px;
        }
    }
    .c-item-body {
        margin-top: 10px; 
        display: flex;
        flex-direction: row;
        align-items: center;
        .c-item-time {
            padding: 4px;
            font-size: 15px;
        }
        .c-item-progressbox {
            flex: 1;
            padding: 0 12px;
        }
        .c-item-text {
            padding: 3px;
            font-size: 22px;
            color: #ffffff;
        }
    }
    .c-item-foot {
        text-align: left;
        font-size: 16px;
        word-break: break-all;
        border-top: solid 1px #aaa;
        padding-top: 5px;
        margin-top: 5px;
        max-height: 100px;
        overflow-y: auto;
        line-height: 20px;
        text-indent: 2em;
    }
}
.c-item-btn() {
    display: block;
    height: 36px;
    width: 36px;
    border-radius: 50%;
    border: solid 2px #ffffff;
    //box-shadow: 0 0 6px #ffffff;
    position: relative;
    margin-right: 8px;
}
