.c-dg-mask {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .3);
    z-index: 199;
    .c-dg-box {
        position: absolute;
        width: 300px;
        height: 220px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: #ffffff;
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        .c-dg-title {
            height: 30px;
            padding: 4px;
            border-bottom: solid 1px #cccccc;
            line-height: 30px;
            font-size: 16px;          
            text-align: center;  
        }
        .c-dg-content {
            flex: 1;
            position: relative;
        }
        .c-dg-footer {
            height: 40px;
            border-top: solid 1px #CCCCCC;
            display: flex;
            flex-direction: row;
            .am-button {
                flex:1;
                height: 40px;
                line-height: 40px;
                &:last-child{
                    color: #108ee9;
                }
            }
        }
    }
}

