.c-rec-panel {
   position: absolute; 
   top: 0;
   left: 0;
   width: 100%;
   z-index: 10;
}
.c-rec-wave {
    height:120px;
    width: 100%;  
    position: absolute;  
    bottom: 0;
    top: 0;
    z-index: 1;
}
.c-recbtn {
    width: 76px;
    height: 76px;
    background: #e94f4f;
    margin: 20px auto 12px;
    border-radius: 50%;    
    display: flex;
    justify-content: center;
    align-items: center;
    .c-recbtn-icon {
        background-color: #FFFFFF;
        
    }
    .c-recbtn-recording{
        width: 30px;
        height: 30px;
        border-radius: 50%;
    }
    .c-recbtn-pause {
        width: 24px;
        height: 24px;
        position: relative;
        &::after {
            content:"";
            position: absolute;
            top: -1px;
            left: 7px;
            width: 10px;
            height:26px;
            background-color: #e94f4f;
        }
    }
}
.c-no-rec {
    font-size: 16px;
    color: #333333;
    margin: 40px auto;
    padding: 0 8px;
}
.c-rectime {
    font-size: 18px;
    text-align: center;
}
